import { Component, OnInit } from '@angular/core';
import { padRight, splitGroups } from '../mask';
import setupValidation from '../validation';


@Component({
  selector: 'app-request-payment-page',
  templateUrl: './request-payment-page.component.html',
  styleUrls: ['./request-payment-page.component.css']
})
export class RequestPaymentPageComponent implements OnInit {
  props = {
    from: {
      regex: /^(\d{10}|\d{12})$/,
      placeholder: '–ò–ù–ù –∏–ª–∏ –Ω–∞–∑–≤–∞–Ω–∏–µ –ø–ª–∞—Ç–µ–ª—å—â–∏–∫–∞',
      errorHint: '–ò–ù–ù - 10 –∏–ª–∏ 12 —Ü–∏—Ñ—Ä',
    },
    bik: {
      type: 'number',
      format: x => splitGroups(padRight(x, 9), [2, 5]).join('-'),
      placeholder: '',
      errorHint: '–í–≤–µ–¥–∏—Ç–µ 9 —Ü–∏—Ñ—Ä',
      min: 0,
      max: 999_999_999,
    },
    account: {
      regex: /./,
      placeholder: '',
      errorHint: '–≠—Ç–æ –ø–æ–ª–µ –Ω—É–∂–Ω–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å',
    },
    nds: {
      regex: /./,
      placeholder: '',
      color: 'red',
      value: '–ù–î–° 18%',
      readonly: true,
      errorHint: '–≠—Ç–æ –ø–æ–ª–µ –Ω—É–∂–Ω–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å',
    },
    nds2: {
      regex: /./,
      placeholder: '',
      color: 'yellow',
      value: '–ù–î–° 10%',
      readonly: true,
      errorHint: '–≠—Ç–æ –ø–æ–ª–µ –Ω—É–∂–Ω–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å',
    },
    nds3: {
      regex: /./,
      placeholder: '',
      color: 'green',
      value: '–ë–µ–∑ –ù–î–°',
      readonly: true,
      errorHint: '–≠—Ç–æ –ø–æ–ª–µ –Ω—É–∂–Ω–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å',
    },
    amount: {
      type: 'number',
      placeholder: '–æ—Ç 1000 –¥–æ 75000‚ÇΩ',
      errorHint: '–í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ –æ—Ç 1000 –¥–æ 75000',
      format: x => `${x}‚ÇΩ`,
      min: 1000,
      max: 75000,
    },
    email: {
      regex: /@/,
      placeholder: '–î–ª—è —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π –æ–± –æ–ø–ª–∞—Ç–µ',
      errorHint: '–í–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç—É –≤ —Ñ–æ—Ä–º–∞—Ç–µ example@mail.ru',
    },
    phone: {
      regex: /^(.*(\+7|8).*)(.*\d.*){10}$/,
      placeholder: '–Ω–∞–ø—Ä–∏–º–µ—Ä +7 999 9999 999',
      format: (x, valid) => {
        if (!valid) return x;
        const numbers = padRight(x.replace(/[^\d]/g, ''), 11, '?').split('');
        const p1 = numbers[0] == '7' ? '+7' : '8';
        const p2 = numbers[1] + numbers[2] + numbers[3];
        const p3 = numbers[4] + numbers[5] + numbers[6] + numbers[7];
        const p4 = numbers[8] + numbers[9] + numbers[10];
        return [p1, p2, p3, p4].join(' ');
      },
      errorHint: '–í–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –≤ —Ñ–æ—Ä–º–∞—Ç–µ +7 999 9999 999',
    },
  }
  nds = 2

  async onSubmit() {
    const toBeSent = {};
    for (let [key, value] of Object.entries(this.props)) {
      if (key.includes('nds')) continue;
      value['color'] = value['valid'] ? 'gray' : 'red-all';
      value['hint'] = value['valid'] ? '' : value['errorHint'];
      toBeSent[key] = value['valueMasked'];
    }
    toBeSent['nds'] = this.nds;
    if (this.isFormValid) {
      alert(`Form is valid, request can be sent: ${JSON.stringify(toBeSent, null, 2)}`);
      await fetch('/api/request-payment', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(toBeSent),
      });
      alert('Sent');
    }
  }

  constructor() { }

  getInvalids = () => ({})
  get isFormValid() {
    return Object.keys(this.getInvalids()).length === 0;
  }

  ngOnInit() {
    this.getInvalids = setupValidation(this.props);
  }
}
